import $ from 'jquery'
import loader from '@/loader/base.config.js'
import inNetworkHbs from '../../templates/county/inNetwork.hbs'
// 
import DigitRoll from '@/loader/common/scripts/digitroll.js'
import { setWidth, randomNumber } from '../common/common.js'
// 
import listHbs from '../../templates/common/dropDownList.hbs'

// 
const COUNTY_MODULE = loader.COUNTY_MODULE
 
export default class InNetwork {
  /**
   *  
   *  @return   {void} 
   */
  constructor() {
    $(`${COUNTY_MODULE} .in-network`).html(inNetworkHbs())
    this.interval = null
  }

  /**
   *  
   *  @param    {object}  data 
   *  @return   {void} 
   */
  render(data) {
    const self = this
    setWidth(`${COUNTY_MODULE} .terminal`, data.inNetwork, 68)
    self.numberRoll(data.inNetwork)
    // 
    // let netData = [...data.operator, ...data.communication]
    let netData = data.operator
    //  flag = 1 hbs
    netData.flag = 1 
    // class
    $('.inNetwork-drop2').html(listHbs(netData))
    self.bindEvent()
  }

  bindEvent() {
    //  
    $(COUNTY_MODULE).off('click').on('click', '.terminal .number', () => {
      const display = $('.inNetwork-drop2').css('display')
      if(display === 'none'){
        $('.inNetwork-drop2').fadeIn()
      }else{
        $('.inNetwork-drop2').fadeOut()
      }
    })
    // $(document).mouseup(function(e){
    //   var _con = $(`${COUNTY_MODULE} .terminal`) // 
    //   if(!_con.is(e.target) && _con.has(e.target).length === 0){  
    //     $('.inNetwork-drop2').hide()
    //   }
    // })
  }

  /**
   *  
   *  @param    {object}  data 
   *  @return   {void} 
   */
  numberRoll(data) {  
    // const self = this 
    //  
    const roll = new DigitRoll({
      container:`${COUNTY_MODULE} .terminal .num-roll`
    })
    // 
    roll.roll(data)
    // 
    let range = 10
    // 
    clearInterval(window.interval1)
    // 
    window.interval1 = setInterval(() => {
      //  (!=0 || null || undefined)
      if(data){
        // 1-10
        let rollTerminal = data - range + randomNumber(1, 10)
        roll.roll(rollTerminal)
      }     
    }, loader.aniTime) 
  }
}



