import $ from 'jquery'
import { deformationToOrigin } from '@/util/zoom/zoom'

/**
 *  
 *  @param    {object}  elem div
 *  @param    {nunber}  data 
 *  @param    {nunber}  w roll
 */

export const setWidth = (elem, data, w) => {
  let len = String(data).length
  //  
  if(len > 8 && elem === '.terminal' || len > 8 && elem === '.local') {
    $('.terminal .number').css('margin-right', '-24px')
    $('.local .value').css('margin-right', '-38px')
  }else if(len < 8 && elem === '.terminal' || len < 8 && elem === '.local'){
    $('.terminal .number').css('margin-right', 0)
  }
   
  $(`${elem} .number`).css('width', len * w)
  $(`${elem} .value`).css('width', len * w)
  
}

/**
 *  
 *  @param    {number}  min 
 *  @param    {number}  max 
 *  @return   {object}  null
 */
export const randomNumber = (min, max) =>{
  let range = max - min
  let rand = Math.random() 
  let num = min + Math.round(rand * range)
  return num
}

/**
 * 
 *  @return   {array}  x,y
 */
const getMousePosition = (evt) =>{
  let pos = deformationToOrigin([evt.clientX , evt.clientY])
  return{
    x: pos[0],
    y: pos[1]
  }
}

/**
 *  
 *  @param    {object}  evt 
 *  @param    {number}  mType   1 top or 2 
 *  @param    {[type]}  curType (province, city, abroad)
 */
export const showAllName = (evt, mType, curType) => {
  const $this = $(evt.target)
  let name = $.trim($this.text())
  let len = 0
  // 
  if(curType === 'city') {
    mType === 1 ? len = 9 : len = 21
  }else {
    mType === 1 ? len = 6 : len = 16
  }
  if(name.length >= len) {
    $this.css('cursor', 'pointer')
    let posi = getMousePosition(evt)
    let left = posi.x
    let top = posi.y - 80
    $('.show-all-name').css({'left': left, 'top': top}).show().text(name)
  }
}




