/**
 * H5
 * @class DigitRoll
 * @desc , webkit, H5. css,  css.
 * @param {object} opts 
 * @param {string} opts.container  selector
 * @param {number} opts.width=1 , 
 * @example
    HTML:
    <div id="num-roll"></div>
 * @example
    js:
    var r1=new DigitRoll({
        container:'#num-roll',
        width:9
    });
 */

export default class DigitRoll {
    constructor(opts) {
      this.container = document.querySelector(opts.container) // 
      let height = opts.height || 120
      this.width = opts.width || 1 
      if (!this.container) {
          throw Error('no container') 
      }
      this.container.style.overflow = 'hidden' 
      this.rollHeight = parseInt(getComputedStyle(this.container).height, 10) //  
      
      this.container.style.height = `${height}px` 
      this.rollHeight = height 
      this.setWidth() 
    }
    
}
/**  @lends DigitRoll */
DigitRoll.prototype = {
    /** 
     * 
     * @param {number} n 
     * @example
        r1.roll(314159);

        //
        setInterval(function(){
            r1.roll(314159);
        },5000)
     */
    roll:function (n) {
        var self = this;
        this.number = parseInt(n, 10) + '';
        if (this.number.length < this.width) {
            this.number = new Array(this.width - this.number.length + 1).join('0') + this.number;
        }else if (this.number.length > this.width) {
            this.width = this.number.length;
            this.setWidth();
        } 
        Array.prototype.forEach.call(this.container.querySelectorAll('.num'), function (item,i) {
            var currentNum = parseInt(item.querySelector('div:last-child').innerHTML, 10); // 
            var goalNum = parseInt(self.number[i], 10); // 
            var gapNum = 0; // 
            var gapStr = '';
            if (currentNum == goalNum) { //  
                return ;
            }else if(currentNum < goalNum) { // 13   
                gapNum = goalNum - currentNum;
                for (var j = currentNum; j < goalNum + 1; j++) {
                    gapStr += '<div>' + j + '</div>'
                }
            }else { //  65   65 9
                gapNum = 10 - currentNum + goalNum;
                for (var j2 = currentNum; j2 < 10; j2++) {
                    gapStr += '<div>' + j2 + '</div>'
                }
                for (var x = 0; x < goalNum + 1; x++) {
                    gapStr += '<div>' + x + '</div>'
                }
            }
            item.style.cssText += '-webkit-transition-duration:0s;-webkit-transform:translateY(0)'; // 
            item.innerHTML = gapStr;
            setTimeout(function () {
                item.style.cssText += '-webkit-transition-duration:1s;-webkit-transform:translateY(-' + self.rollHeight * gapNum + 'px)'
            },50)
        })
    },
    /** 
     * 
     * @desc   
     * @param {number} n  
     * @example
        r1.setWidth(10);
     */
    setWidth:function (n) {
        
        var num = n || this.width;
        var str = '';
        for (var i = 0; i < num; i++) {
            str += '<div class="num" style="float:left;height:100%;line-height:' + this.rollHeight + 'px"><div>0</div></div>';
        }
        this.container.innerHTML = str;
    }
}




