import $ from 'jquery'
import loader from '@/loader/base.config.js'
import areaHbs from '../../templates/common/areaList.hbs'
import hbs from '../../templates/common/header.hbs'
import { fetch, fetchSocket } from '@/util/request'
import userHbs from '../../templates/common/user.hbs'
import County from '../county'

// 
const COUNTY_MODULE = loader.COUNTY_MODULE // 
const MAIN_MODULE = loader.MAIN_MODULE // 
// 
const county = new County()
let areaNams = null

export default class Header {
  
  /**
   *  
   *  @return   {object}  null
   */
  constructor() {
    // 
    $('.dynamic-header').html(hbs())
    $('.header-info').html(userHbs())
  }

  /**
   *  
   *  @return   {object}  null
   */
  render() {
    const self = this
    // 
    fetch('fetchUser', (data) => {
      $('.user-name').text(data)
    })
    // 
    self.renderAreaList()
    // 
    self.renderTime()
    // 
    self.exitSystem()
  }

  /**
   *  
   *  @example: [example]
   *  @return   {[type]}  [description]
   */
  renderAreaList() {
    const self = this
    // 
    fetch('fetchAreaList', (data) => {
      areaNams = data.areaList // 
      // 
      areaNams = areaNams.slice(0, areaNams.length - 1)
      $('.area-list').html(areaHbs(areaNams))
    })
    // 
    self.bindEvent()
    // 
    self.searchEvent()
  }

  bindEvent() {
 
    let setTime = null
    // 
    $('.area-name').on('click', () => {
      const display = $('.area-list').css('display')
      if(display === 'none'){
        $('.area-list').show()
      }else{
        $('.area-list').hide()
      }
      $('.area-list').html(areaHbs(areaNams))
    })

    // 
    $('.input-area').on('dblclick', (evt) => {
      clearTimeout(setTime)
      const $this = $(evt.target)
      $this[0].value = ''
    })

    // 
    $('.area-list').hover((evt) => {
      const $this = $(evt.target)
      $this.fadeIn()
    }, () => {
      $('.area-list').hide()
    })

    // 
    $(document).mouseup(function(e){
      let _con = $('.area-name') || $('.area-name') // 
      if(!_con.is(e.target) && _con.has(e.target).length === 0){ // Mark 1
        $('.area-list').fadeOut()
      }
    })

    // 
    $('.area-list').off('click', 'li').on('click', 'li', (evt) => {
      $('.drop-down-list').hide()
      $('.dynamic-county-templates .operator-svg rect').hide()
      // top 
      $('.area-top .nav').eq(0).addClass('active').siblings().removeClass('active')
      const $this = $(evt.target)
      let text = $this.text()
      $('.input-area').val(text)
      // 
      $('.county-name').text(text)
      // top10
      if(text === '') {
        text = ''
      }
      $('.yu-type').text(text)
      // id()
      const mapId = $this.attr('areaId')
      // 
      const areaId = $this.attr('oldId')
      // 
      $('.area-list').fadeOut()
      // if else 
      if(mapId === '500000'){
        $(MAIN_MODULE).slideDown(800)
        $(COUNTY_MODULE).slideUp(800)
      }else{
        // id
        county.init(mapId, areaId)
        $(MAIN_MODULE).slideUp(300)
        $(COUNTY_MODULE).slideDown(300)
      }
    })
  }

  /**
   *  
   *  @example: [example]
   *  @return   {[type]}  [description]
   */
  searchEvent() {
    const self = this
    // 
    $('.input-area').on('keyup', (evt) => {
      let keyCode = evt.keyCode
      // 8
      if(keyCode === 8) {
        return false
      }
      self.renderSearch()
    })
  }

  /**
   *  
   *  @example: [example]
   *  @return   {[type]}  [description]
   */
  renderSearch() {
    let text = $.trim($('.input-area').val())
    if(text === '') {
      text = 'all' // 
    }
    fetch('fetchAreaSearch', {text}, (data) => {
      $('.area-list').html(areaHbs(data.areaList))
      if(data.areaList.length === 0) {
        $('.main-list').html('')
      }else{
        $('.main-list').css('display', 'none')
      }
      // 
      if(text === 'all') {
        $('.main-list').html('').css('display', 'block')
      }
      $('.area-list').fadeIn()
    })
  }

  /**
   *  
   *  @return   {object}  null
   */
  exitSystem() {
    $('.out-login').on('click', () => {
      fetch('fetchLogout', (data) => {
        window.location.href = data
      })
    })
  }

  /**
   *  
   *  @return   {object}  null
   */
  renderTime() {
    const self = this
    // 
    // setInterval(() => {
    //   self.getCurrentDate()
    // }, 1000)

    // 
    self.getServerDate()
  }

  /**
   *  
   *  @return   {object}  null
   */
  getServerDate() {
    fetchSocket('fetchTimewebsocket', 1, (data) => {
      let date = JSON.parse(data.data) 
      date = date.result
      const year = date.slice(0, 4)
      const month = date.slice(4, 6)
      const day = date.slice(6, 8)
      const housr = date.slice(8, 10)
      const minutes = date.slice(10, 12)
      let time = `${year}${month}${day}  ${housr}:${minutes}`
      $('.current-time').text(time)
    })
  }

  /**
   *  
   *  @return   {object}  null
   */
  getCurrentDate() {
    const self = this
    const date = new Date()
    const year = date.getFullYear()
    const month = self.formateDate(date.getMonth() + 1) 
    const day = date.getDate()
    const housr = self.formateDate(date.getHours())
    const minutes = self.formateDate(date.getMinutes())
    let time = `${year}${month}${day}  ${housr}:${minutes}`
    $('.current-time').text(time)
  }
 
  /**
   *  
   *  @param    {number}  t 
   *  @return   {string}  
   */
  formateDate (t) {
    if (t < 10) {
      return `0${t}`
    }
    return t
  }
}



