
export default {
  url: '/county/areaTop10/:areaId',
  mock: {
    'code': 1,
    'msg': 'success',
    'result': {
      'areaTop10': {
        'province|10': [
          {
            'name': '@province',
            'value|1-9999999': 1,
            'id|+1': ['500242000000', '500236000000', '500238000000', '500101000000', '500229000000'],
            'increase|1-100': 1,
            'flag|1': [1, -1, 0]
          }
        ],
        'city|10': [
          {
            'name|+1': ['()', '()', '()', '(', 
              '()', '()', '()', '()', '()', 
              '()'],
            'value|1-100000': 1,
            'id|+1': ['500242000000', '500236000000', '500238000000', '500101000000', '500229000000'],
            'increase|1-100': 1,
            'flag|1': [1, -1, 0]
          }
        ],
        'abroad|10': [
          {
            'name': '@pick(["", "", "", "", ""])',
            'value|1-9999999': 1,
            'id|+1': ['500242000000', '500236000000', '500238000000', '500101000000', '500229000000'],
            'increase|1-100': 1,
            'flag|1': [1, -1, 0]
          }
        ]
      }
    } 
  }
}


