import Mock from 'mockjs'
const Random = Mock.Random

export default {
  url: '/main/inYuTop10',
  mock: {
    'code': 1,
    'msg': 'success',
    'result': {
      'inYuTop10': {
        'province|10': [
          {
            'name': '@province',
            'id|+1': ['500242000000', '500236000000', '500238000000', '500101000000', '500229000000'],
            'value|1-1000000000': 1,
            'increase': Random.float(1, 100, 1, 1),
            'flag|1': [0, 1, -1]
          }
        ],
        'city|10': [
          {
            'name|+1': ['()', '()', '()', '(', '()', '()', '()', '()', '()', '()'],
            'value|1-100000': 1,
            'id|+1': ['500242000000', '500236000000', '500238000000', '500101000000', '500229000000'],
            'increase': Random.float(1, 100, 1, 1),
            'flag|1': [0, 1, -1]
          }
        ],
        'abroad|10': [
          {
            'name': '@pick(["", "", "", "", ""])',
            'value|1-100000000': 1,
            'id|+1': ['500242000000', '500236000000', '500238000000', '500101000000', '500229000000'],
            'increase': Random.float(1, 100, 1, 1),
            'flag|1': [0, 1, -1]
          }
        ]
      }
    }
  }
}



