import $ from 'jquery'
import { fetch, fetchInterval } from '@/util/request'
import loader from '@/loader/base.config.js'
import inNetworkHbs from '../../templates/main/inNetwork.hbs'
import listHbs from '../../templates/common/dropDownList.hbs'
import controlDettialHbs from '../../templates/main/controlDettial.hbs'
import topList from '../../templates/common/controlList.hbs'

// 
import DigitRoll from '@/loader/common/scripts/digitroll.js'
import { setWidth, randomNumber } from '../common/common.js'
// class
const container = $('.control-detials-dialog')
// 
const MAIN_MODULE = loader.MAIN_MODULE

export default class InNetwork {
  /**
   *  
   *  @return   {object}  null
   */
  constructor() {
    $(`${MAIN_MODULE} .in-network`).html(inNetworkHbs())
    container.html(controlDettialHbs())
    this.interval = null
  }

  /**
   *  
   *  @return   {object}  null
   */
  render() {
    const self = this
    // 10 
    fetchInterval('fetchInNetwork', (data) => {
      let inNetworkData = data.inNetwork
      // number
      setWidth(`${MAIN_MODULE} .terminal`, inNetworkData.terminal, 65)
      // number
      setWidth(`${MAIN_MODULE} .peopleNumer`, inNetworkData.controlArea, 65)
      // 
      self.numberRoll(inNetworkData)
      // 
      data.length = data.operator.length
      // let netData = [...data.operator, ...data.communication]
      let netData = data.operator
      //  flag = 1 hbs
      netData.flag = 1 
      // 
      $('.inNetwork-drop').html(listHbs(netData))
      // 
      const dropDown = $('.contral-drop')
      const areaData = data.areaClassifyCount
      data.length = areaData.length
      areaData.flag = 2
      dropDown.html(listHbs(areaData))
      // append
      if(areaData.length !== 0){
        dropDown.find('.list-container')
          .addClass('control-drop')
          .append('<div class="control-all-btn"> +</div>')  
      } 
     
      // 
      self.bindEvent(data)
    }, loader.updateTime)
  }

  /**
   *  
   *  @param    {object}  data 
   *  @return   {void}   
   */
  bindEvent() {
    const self = this
    // class
    const dropDown = $('.contral-drop')
    //  
    $(MAIN_MODULE).off('click', '.terminal .number')
      .on('click', '.terminal .number', (evt) => {
        evt.stopPropagation()
        evt.preventDefault()
        const display = $('.inNetwork-drop').css('display')
        if(display === 'none'){
          $('.inNetwork-drop').fadeIn()
        }else{
          $('.inNetwork-drop').fadeOut()
        }
      })

    // 
    $('.peopleNumer').off('click', '.number').on('click', '.number', function(evt){
      evt.stopPropagation()
      evt.preventDefault()
      const display = $('.contral-drop').css('display')
      if(display === 'none'){
        $('.contral-drop').fadeIn()
      }else{
        $('.contral-drop').fadeOut()
      }
    })
 
    // 
    dropDown.on('click', '.control-all-btn', () => {
      $('.control-text').val('').attr('placeholder', '')
      fetch('fetchControlDetails', (controlData) => {
        dropDown.fadeOut() // 
        container.fadeIn().find('.search-list')
          .html(topList(controlData.controlAreaDetails))
      })
    })

    // 
    container.on('click', '.close', () => {
      container.hide()
    })

    // 
    $('.control-search').on('click', () => {
      $('.search-list').scrollTop(0)
      self.renderSearch()
    })

    // inputkeyCode()
    $('.control-text').on('keypress', (evt) => {
      let keyCode = evt.keyCode
      // 
      if(keyCode === 13){
        $('.search-list').scrollTop(0)
        self.renderSearch()
      }
    })

    // -------------------------------
    // 
    // $(document).mouseup(function(e){
    //   var _con = $('.terminal') // 
    //   if(!_con.is(e.target) && _con.has(e.target).length === 0){  
    //     $('.inNetwork-drop').hide()
    //   }
    // })
    // // 
    // $(document).mouseup(function(e){
    //   var _con = $('.peopleNumer') // 
    //   if(!_con.is(e.target) && _con.has(e.target).length === 0){  
    //     $('.contral-drop').hide()
    //   }
    // })
  }

  /**
   *  
   *  @return   {object}  null
   */
  renderSearch() {
    let text = $('.control-text').val()
    $('.control-text').attr('value', text)
    if(text === '') {
      text = 'all' // 
    }
    // 
    fetch('fetchControlSearch', {text}, (data) => {
      $('.search-list').html(topList(data.searchList))
    })
  }

  /**
   *  
   *  @param    {object}  data 
   *  @return   {object}  null
   */
  numberRoll(data) {
    const self = this
    // 
    let terminal = data.terminal 
    //  
    let controlArea = data.controlArea  
    /**
     *  
     */
    //  container(num)
    const roll1 = new DigitRoll({
      container: `${MAIN_MODULE} .terminal .num-roll` 
    })
    //  container
    const roll2 = new DigitRoll({
      container:`${MAIN_MODULE} .peopleNumer .num-roll`
    })
    // 
    roll1.roll(terminal)
    // 
    roll2.roll(controlArea)
    // 
    let range = 10
    // 
    clearInterval(self.interval)
    // 
    self.interval = setInterval(() => {
      //  (!=0 || null || undefined)
      if(terminal){
        // 1-10
        let rollTerminal = terminal - range + randomNumber(1, 10)
        roll1.roll(rollTerminal)
      }
      // (!=0 || null || undefined)
      if(controlArea){
        // 1-10
        let rollControlArea = controlArea - range + randomNumber(1, 10) 
        roll2.roll(rollControlArea)
      }
      
    }, loader.aniTime) 
  }
}



