
import Mock from 'mockjs'
const Random = Mock.Random

export default {
  url: '/main/outYuTop10',
  mock: {
    'code': 1,
    'msg': 'success',
    'result': {
      'outYuTop10': {
        'province|10': [
          {
            'name': '@province',
            'value|1-9999999': 1,
            'increase': Random.float(1, 100, 1, 1),
            'flag|1': [1, -1, 0]
          }
        ],
        'city|10': [
          {
            'name|+1': ['()', '()', '()', '(', '()', '()', '()', '()', '()', '()'],
            'value|1-100000': 1,
            'increase': Random.float(1, 100, 1, 1),
            'flag|1': [1, -1, 0]
          }
        ],
        'abroad|10': [
          {
            'name': '@pick(["", "", "", "", ""])',
            'value|1-9999999': 1,
            'increase': Random.float(1, 100, 1, 1),
            'flag|1': [1, -1, 0]
          }
        ]
      }
    } 
  }
}


