import $ from 'jquery'
import { fetch } from '@/util/request'
import topDeitalsList from '../../templates/common/topDeitalsList.hbs'

export default class TopSearch {

  /**
   *  
   *  @param    {string}  areaId 
   *  @param    {string}  type   (province, city, abroad)
   *  @return   {void}   
   */
  bindEvent(areaId, type) {
    const self = this
    // 
    $('.search-btn').off('click').on('click', () => {
      $('.search-list').scrollTop(0)
      self.renderSearch(areaId, type)
    })

    // inputkeyCode
    $('.search-text').off('keypress').on('keypress', (evt) => {
      let keyCode = evt.keyCode
      // 
      if(keyCode === 13){
        $('.search-list').scrollTop(0)
        self.renderSearch(areaId, type)
      }
    })
  }

  /**
   *  
   *  @param    {string}  areaId 
   *  @param    {string}  type   (province, city, abroad)
   *  @return   {void}   
   */
  renderSearch(areaId, type) {
    let text = $('.search-text').val()
    $('.search-text').attr('value', text)
    // 
    text = $.trim(text)
    if(text === '') {
      text = 'all' // 
    }
    fetch('fetchCSearch', {areaId, type, text}, (data) => {
      // 
      $('.search-list').html(topDeitalsList(data.searchList))
    })
  }

  /**
   *  
   *  @param    {string}  areaId 
   *  @param    {string}  type   (province, city, abroad)
   *  @return   {void}   
   */
  init(areaId, type) {
    const self = this
    self.bindEvent(areaId, type)
  }
} 



