import $ from 'jquery'
import loader from '@/loader/base.config.js'
import hbs from '../../templates/county/phoneNumber.hbs'
// 
import DigitRoll from '@/loader/common/scripts/digitroll.js'
import { setWidth } from '../common/common.js'
// class 
const COUNTY_MODULE = loader.COUNTY_MODULE

export default class PhoneNumber {
  /**
   *  
   *  @return   {object}  null
   */
  constructor() {
    // 
    $(`${COUNTY_MODULE} .phone-number`).html(hbs())
    this.interval = null
  }
  /**
   *  
   *  @param    {object}  data 
   *  @return   {object}  null
   */
  render(data) {
    const self = this
    // 
    const width = 44 // 
    // 
    setWidth(`${COUNTY_MODULE} .local`, data.local, width)
    // 
    setWidth(`${COUNTY_MODULE} .nonlocal`, data.nonlocal, width)
    // 
    setWidth(`${COUNTY_MODULE} .abroad`, data.abroad, width)
    self.numberRoll(data)
  }

  /**
   *  
   *  @param    {object}  data 
   *  @return   {object}  null
   */
  numberRoll(data) {
    const self = this
    /**
     *  
     */
    // 
    const localRoll = new DigitRoll({
      container:`${COUNTY_MODULE} .local .num-roll`
    })
    // 
    const nonlocalRoll = new DigitRoll({
      container:`${COUNTY_MODULE} .nonlocal .num-roll`
    })
    // 
    const abroadRoll = new DigitRoll({
      container:`${COUNTY_MODULE} .abroad .num-roll`
    })

    // 
    let local = data.local
    localRoll.roll(local)

    // 
    let nonlocal = data.nonlocal
    nonlocalRoll.roll(nonlocal)

    // 
    let abroad = data.abroad
    abroadRoll.roll(abroad)
    // 
    let range = 10
    // 
    clearInterval(self.interval)
    self.interval = setInterval(function () {
      // 10
      let random = parseInt(Math.random() * 10, 10) 
      // 
      let rollLocal = local - range + random
      localRoll.roll(rollLocal)
      // 
      let rollNonlocal = nonlocal - range + random
      nonlocalRoll.roll(rollNonlocal)
      // 
      let rollAbroad = abroad - range + random
      abroadRoll.roll(rollAbroad)
    }, loader.aniTime)

  }

}



